.body {
    /* margin-left: 0.3rem; */
    /* margin-right: 0.3rem; */
}

.footnote-body {
    margin-left: 1rem;
    margin-right: 1rem;
}

code {
    font-family: $font_mono;
}

pre {
    font-family: $font_mono;
}

ul.tags {
    list-style: none;
    padding-left: 0.3rem;
    padding-right: 0.3rem;
}

ul.tags li {
    /* display: inline; */
    /* padding-bottom: 1rem; */
    font-family: $font_mono;
}

.tags li::before{
  /* content: "🏷️"; */
}

.site-description {
    display: flex;
    justify-content: space-between;
    font-family: $font_default;
    font-size: 1.0rem;
}

.site-description > blockquote {
    background-color: #cccccc;
    box-shadow: 9px 9px 0px 2px green; 
}


.title > .meta {
    // font-size: 0.8rem;
    font-family: $font_mono;
    margin-left: 0.3rem;
}

blockquote {
    padding: .1em .2em;
    border: solid 1px ;
}

figure {
  box-sizing: border-box;
  display: inline-block;
  margin: 0;
  max-width: 100%;
}

figure img {
  /* max-height: 500px; */
}

/* Containers */
.content {
  margin-bottom: 1em;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1ch;
  word-wrap: break-word;
}

/* Header */
header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 1em 0;
    align-items: center;
}

.main a {
    font-size: 3.0rem;
    font-family: $font_mono;
}


header nav a {
    font-family: $font_mono;
    font-size: 1.0rem;
    /* padding-bottom: 0.4rem; */
    margin-left: 1.3rem;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 1.2rem;
  margin-top: 2em;
}



section.list-item {
    border-bottom: dashed 1px red;
}

h1.title::before {  content: '# '; }

/*
h2::before {  content: '## '; }
