h4::before {  content: '#### '; }
h5::before {  content: '##### '; }
h6::before {  content: '###### '; }
*/

/* Footer */
footer {
  align-items: center;
  border-top: 0.1rem dashed;
  padding: 1rem 0rem;
  margin-top: 2rem;
}

.soc {
  display: flex;
  align-items: center;
  padding-right: 1rem;
  margin-right: 1rem;
  border-right: 2px solid;
  border-bottom: none;
}

.footer-info {
    text-align: center;
    font-size: 0.9rem;
}

/* Common */
.title h1 {
  margin-bottom: 0;
}

/* Posts */
article .title {
  margin-bottom: 1em;
}


/* Callout */
.callout {
  padding: 1em;
}

.callout p {
  /* font-family: monospace; */
  margin: 0;
}

.callout a {
}

.callout a:hover {
}

